/* background image and welcome text */
.intro {
    position: relative;
    text-align: center;
    margin-top: 70px;
}

.intro img {
    width: 100%;
}

.centered_text {
    position: absolute;
    top: 30%;
    left: 50%;
    transform: translate(-50%, -50%);
    text-decoration: none;
    color: white;
}

.centered_text h1 {
    font-size: 60px;
    border: 5px solid white;
}

.centered_text span {
    font-size: 30px;
}

/* custom scrollbar */
::-webkit-scrollbar {
    width: 10px;
    background-color: black;
}

::-webkit-scrollbar-thumb {
    background-color: white;
    border-radius: 10px;
    box-shadow: inset 0 0 3px black;
}
