/* loading page gif */
.loading_page_gif {
    margin: 0;
    padding: 0;
    position: fixed;
    top: 0;
    left: 0;
    height: 100%;
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
}

/* scroll */
.scroll_to_top_button {
    position: fixed;
    right: 10px;
    bottom: 10px;
    width: 50px;
    height: 50px;
    border-radius: 50%;
    background-color: rgb(250,128,114);
    cursor: pointer;
}

.scroll_to_top_button:hover {
    background: rgb(224, 240, 152);
}

/* error page */
.error_page {
    margin: 0;
    padding: 0;
    font-family: "montserrat", sans-serif;
    min-height: 100vh;
    background-image: linear-gradient(125deg,grey,white);
    text-align: center;
    position: relative;
}

.error_page div {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    text-decoration: none;
    color: black;
    font-size: 60px;
}

.error_page div h2 {
    font-size: 40px;
    text-decoration: none;
    color: black;
}

.error_page div h1 {
    font-size: 160px;
    font-weight: 900;
    text-decoration: none;
    color: black;
}

.error_page div span {
    font-size: 20px;
    text-decoration: none;
    color: black;
}

/* card */
.card {
    height: 100%;
    margin-top: 10px;
    margin-bottom: 10px;
}

.card-img-top {
    width: 100%;
    /* height: 10vw; */
    object-fit: cover;
}

.card-title {
    color: black;
    cursor: pointer;
    text-decoration: none;
    text-transform: uppercase;
}

.card-text {
    color: black;
}