#NavigationBar {
    position: fixed;
    top: 0;
    z-index: 1;
    display: flex;
    width: 100%;
    min-height: 59px;
    font-size: 1rem;
}

.nav_links {
    list-style: none;
    display: flex;
    width: 100%;
    justify-content: center;
}

.nav_item {
    margin: 0 1em;
    padding: 1em;
}

.nav_link {
    position: relative;
    text-decoration: none;
    text-transform: uppercase;
    font-weight: 599;
    cursor: pointer;
}

.nav_link:after {
    position: absolute;
    bottom: -3px;
    left: 0;
    content: "";
    width: 0;
    height: 3px;
    transition: all 0.2s linear;
}

/* light and dark mode */
.light_mode_nav {
    color: white;
    background: #1976d2;
}

.light_mode_nav .nav_link:hover::after {
    width: 100%;
    background-color: white;
}

.dark_mode_nav {
    color: white;
    background: black;
}

.dark_mode_nav .nav_link:hover::after {
    width: 100%;
    background-color: white;
}

.dark_mode_icon {
    margin: 0 1em;
    padding: 1em;
    cursor: pointer;
}
